.go-to-top a {
  background-color: #1565c0;
  box-shadow: 0 12px 35px -10px rgba(21, 101, 192, 0.55), 0 8px 10px -5px rgba(0, 0, 0, 0.09), 0 4px 25px -2px rgba(0, 0, 0, 0.14);
}

.go-to-top a:hover, .go-to-top a:focus {
  background-color: #1977e2;
  box-shadow: 0 1px 9px 0 rgba(21, 101, 192, 0.5);
}


/********/
/* Mapa */
/********/


.kbf-map-info, .kbf-provinces-label {
  position: absolute;
  background: #333333;
  color: white;
  font-family: Montserrat, sans-serif;
}

.kbf-provinces-label {
  font-size: 11px;
  padding: 4px;
  background: #173045;
  border-radius: 8px;
}

/************************/
/* Panel kontrolny mapy */
/************************/

#kbf-map-panel {
  position: absolute;
  z-index: 5000;
  top: 0;

}

@media (max-width: 320px) {
  #kbf-map-panel .card-title {
    display: none;
  }
}

@media (max-height: 480px){
  #kbf-map-panel .card-title {
    display: none;
  }
}

@media (min-width: 960px) {

  #kbf-map-panel {
    top: 2rem;
    left: 2rem;
    width: 35%;
  }
}

@media (min-width: 1140px) {

  #kbf-map-panel {
    width: 30%;
  }
}

@media (min-width: 1280px) {

  #kbf-map-panel {
    width: 25%;
  }
}

#kbf-map-panel .btn {
  padding: .625rem 1rem;
}

#kbf-map-panel button {
  width: 100%;
}

#kbf-map-panel h5 {
  color: #565656;
  font-weight: normal;
}

#kbf-map-panel .kbf-map-panel-show-all, #kbf-map-panel .kbf-map-panel-show-province {
  cursor: pointer;
  color: #2196f3;
}

#kbf-map-panel .kbf-map-panel-show-all:hover {
  opacity: 0.8;
}

#kbf-map-panel .dropdown-menu.show {
  max-height: 610px !important;
  overflow-y: hidden;
}

@media (max-height: 1024px) {
  #kbf-map-panel .dropdown-menu.show {
    max-height: 585px !important;
  }
}

@media (max-height: 1024px) and (orientation: portrait) {
  #kbf-map-panel .dropdown-menu.show {
    max-height: 485px !important;
  }
}


@media (max-height: 780px) {
  #kbf-map-panel .dropdown-menu.show {
    max-height: 350px !important;
  }

  #kbf-map-panel .card-body {
    padding-top: 1rem !important;
    padding-bottom: 1.2rem !important;
  }
}

@media (max-height: 480px) {
  #kbf-map-panel .dropdown-menu.show {
    max-height: 320px !important;
  }
}

.kbf-map-show-companies {
  position: absolute;
  width: 12rem;
  z-index: 5000;
  bottom: 2rem;
  left: 50vw;
  transform: translate3d(-50%, 0, 0);
}

/*Scrollbar*/
.ps__rail-y {   opacity: 0.6 !important; background-color: #eee !important; width: 16px !important; }
.ps__thumb-y { width: 11px !important; }